.azzle-content{
    &-thumb{
        text-align: center;
        position: relative;
        @include max (991px){
            padding-bottom: 40px;
        }
        img{
            border-radius: 10px;
            
            @include max (767px){
                width: 100%;
                height: 100%;
            }
        }
        &-bg{
            position: absolute;
            bottom: 0;
            z-index: -1;
            @include max(991px) {
                display: none;
            }
        }
    }
    &-bg{
        img{
            @include max (991px){
                width: 100%;
            }
        }
    }
    // start there
    &-thumb2{
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        img{
            border-radius: 20px;
            width: 100%;
            border: 5px solid var(--heading-color);
        }
    }
}
.azzle-feature{
    &-section{
        margin-top: -120px;
    }
    &-wraper{
        display: flex;
        align-items: center;
        gap: 24px;
    }
    &-item{
        border-radius: 3px;
        padding: 30px;
        background-color: var(--light-color6);
        &.item2{
            margin-bottom: 24px;
        }
    }
    &-title{
        padding-bottom: 24px;
        display: flex;
        gap: 24px;
        align-items: center;
        h3{
            color: var(--light-color);
            font-size: 30px;
            line-height: 1.5em;
            letter-spacing: -1px;
        }
    }
    &-data{
        p{
            color: var(--light-color);
        }
    }
    &-btn{
        padding-top: 30px;
    }
}

.azzle-content-slider{
    &-init{
        padding: 20px 0;
        height: 70px;
        overflow: hidden;
        background-color: var(--variable-color6);
    }
    &-item{
        font-weight: 700;
        font-size: 32px;
        line-height: 42px;
        letter-spacing: 0px;
        text-align: right;
        text-transform: uppercase;
    }
}


// service single
.azzle-service-box{
    &-column{
        gap: 24px;
        display: grid;
        align-items: center;
        grid-template-columns: repeat(2, 1fr);
        @include max(767px) {
            grid-template-columns: repeat(1, 1fr);
        }
    }
    img{
       margin-bottom: 10px; 
    }
    h3{
        margin-bottom: 15px;
        padding-bottom: 0!important;
    }
    p{
        font-size: 18px;
    }
}

.azzle-target-list{
    &-wrap{
        display: flex;
        gap: 24px;
        flex-direction: column;
    }
    gap: 15px;
    display: flex;
    flex-direction: column;
    p{
        font-size: 18px;
    }
}