.azzle-team{
    &-wrap{
        border-radius: 20px;
        padding: 30px;
        transition: all 0.4s;
        margin-bottom: 24px;
        background-color: var(--light-color5);
        @include max(991px) {
            padding: 24px;
        }
        &:hover{
            .azzle-team-thumb img{
                transform: scale(1.1);
            }
        }
        &.wrap2{
            background-color: var(--light-color2);
        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 20px;
        img{
            border-radius: 00px;
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
        }
    }
    &-content{
        padding: 24px 0 10px 0;
        h3{
            font-family: $heading-font3;
            font-size: 30px;
            line-height: 1.5em;
            @include max(991px) {
                font-size: 24px;
            }
        }
    }
    &-author-wrap{
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include max(991px) {
            gap: 15px;
            align-items: flex-start;
            flex-direction: column;
        }
    }
}

.azzle-team-single{
    height: 96%;
    display: flex;
    padding: 30px;
    margin-bottom: 24px;
    text-align: center;
    border-radius: 20px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--light-color2);
    @include max(1199px) {
        height: 100%;
    }
    h3{
        font-size: 40px;
        line-height: 48px;
        margin-bottom: 12px;
    }
    p{
        margin: 0;
    }
}

.azzle-single-team-top{
    padding-bottom: 80px;
    margin-bottom: 80px;
    border-bottom: 1px solid var(--variable-color1);
    @include max(767px) {
        padding-bottom: 60px;
        margin-bottom: 60px;
    }
}

.azzle-single-team-bottom{
    display: grid;
    row-gap: 40px;
    column-gap: 30px;
    grid-template-columns: repeat(3, 1fr);
    @include max(1199px) {
        grid-template-columns: repeat(2, 1fr);
    }
    @include max(767px) {
        grid-template-columns: repeat(1, 1fr);
    }
    h3{
        margin-bottom: 15px;
    }
}

.responsive-mb-40{
    @include max(991px) {
        margin-bottom: 40px;
    }
}