.azzle-blog{
    &-wrap{
        border-radius: 20px;
        margin-bottom: 24px;
        transition: all 0.4s;
        background-color: var(--light-color);
        border: 1px solid var(--variable-color1);
        &:hover{
            border: 1px solid var(--light-color);
            box-shadow: 0px 4px 80px 0px rgba(0, 0, 0, 0.05);
            .azzle-blog-thumb img{
                transform: scale(1.1);
            }
        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 20px;
        img{
            border-radius: 20px;
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
        }
    }
    &-content{
        padding: 24px 20px;
        h3{
            font-weight: 500;
            font-family: $heading-font2;
            transition: all 0.4s;
            &:hover{
              color: var(--variable-color5);
            }
        }
    }
    &-meta{
        ul{
            li{
                a{
                    color: var(--light-color4);
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 30px;
                }
            }
        }
    }
    &-data-wrap{
        padding-top: 24px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    // end two
    &-wrapper{
        display: flex;
        align-items: center;
        gap: 24px;
        border-radius: 20px;
        padding: 30px 20px;
        margin-bottom: 24px;
        transition: all 0.4s;
        background-color: var(--light-color5);
        @include max (1199px){
            display: block;
        }
        @include max (991px){
            display: flex;
        }
         @include max (767px){
            display: block;
        }
        &:hover{
            .azzle-blog-thumb2 img{
                transform: scale(1.1);
            }
        }

    }
    &-wrap2{
        @include max (1199px){
            padding-top: 40px;
        }
        @include max (991px){
            padding-top: 0px;
        }
        @include max (767px){
            padding-top: 30px;
        }
    }
    &-thumb2{
        position: relative;
        overflow: hidden;
        flex-shrink: 0;
        border-radius: 10px;
        a{
            img{
                border-radius: 10px;
                width: 100%;
                height: 100%;
                object-fit: cover;
                transition: all 0.4s;
            }
        }
    }
    &-meta{
        ul{
            li{
                display: inline-block;
                font-size: 14px;
                line-height: 1.43em;
                a{
                    font-size: 14px;
                    line-height: 1.43em;
                    transition: all 0.4;
                    color: var(--variable-color2);
                    &:hover{
                        color: var(--variable-color5);
                    }
                }
            }
        }
    }
    &-content2{
        a{
            h3{
                transition: all 0.4s;
                font-family: $heading-font3;
                font-size: 30px;
                line-height: 1.5em;
                padding: 24px 0 12px 0;
                &:hover{
                    color: var(--variable-color5);
                }
            }
        }
        p{
            color: var(--variable-color2);
        }
    }
}






// blog page

.single-post-item {
  margin-bottom: 24px;
  overflow: hidden;
  transition: .4s;
  border-radius: 10px;
  border: 1px solid var(--variable-color1);
}
.post-thumbnail{
  overflow: hidden;
}
.post-thumbnail img {
  transition: all 0.5s;
  transform: scale(1);
}
.single-post-item:hover .post-thumbnail img{
  transform: scale(1.1);
}

.post-content{
  padding: 24px;
  p{
    font-size: 18px;
    line-height: 30px;
  }
}
.post-meta {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.post-category a{
  display: inline-block;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: var(--heading-color);
  position: relative;
  padding-right: 20px;
  transition: all 0.4s;
  &:hover{
    color: var(--accent-color);
  }
  &::before{
    content: "";
    right: 0;
    top: 8px;
    width: 7px;
    height: 7px;
    border-radius: 10px;
    position: absolute;
    background-color: var(--heading-color);
  }
}
.post-date{
  font-size: 16px;
  line-height: 24px;
  padding-left: 10px;
}
h3.entry-title{
  margin-bottom: 15px;
  transition: all 0.4s;
  &:hover{
    color: var(--accent-color);
  }
}



// sidebar

.right-sidebar{
  @include max(991px) {
    margin-top: 50px;
  }
}

.widget {
  margin-bottom: 30px;
  padding: 30px;
  border-radius: 10px;
  border: 1px solid var(--variable-color1);
  &:first-child{
    padding: 0;
    background-color: transparent;
    border: none;
  }
  h3{
    margin-bottom: 34px;
    font-size: 24px;
    font-weight: 600;
    position: relative;
    display: inline-block;
    &::before{
        content: "";
        left: 0;
        bottom: -8px;
        width: 100%;
        height: 2px;
        position: absolute;
        background-color: var(--heading-color);
    }
  }
  p{
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 15px;
  }
  ul{
    li{
      padding-bottom: 20px;
      &:last-child{
        padding-bottom: 0;
      }
      a{
        font-size: 18px;
        transition: all 0.4s;
        color: var(--body-color);
        transition: all 0.4s;
        &:hover{
            color: var(--accent-color);
        }
      }
    }
  }
}
.widget.azzle_recent_posts_Widget .post-date {
  margin-bottom: 10px;
}
.post-item .post-title {
  font-size: 16px;
  font-weight: 600;
  color: var(--heading-color);
  line-height: 24px; /* 133.333% */
  transition: all 0.4s;
  
}

.post-text{
    .post-date{
        padding-left: 0;
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 10px;
        color: var(--heading-color);
    }
    p{
        font-size: 16px;
        font-weight: 600;
        line-height: 24px;
        margin: 0;
        transition: all 0.4s;
        color: var(--heading-color);
        &:hover{
          color: var(--accent-color);
        }
    }
}

.post-item{
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  @include max(1400px) {
    display: block;
  }
  @include max(991px) {
    display: flex;
  }
  @include max(400px) {
    display: block;
  }
  &:last-child{
    margin-bottom: 0;
  }
}
.post-thumb {
  width: 150px;
  height: 100px;
  flex-shrink: 0;
  margin-right: 15px;
  @include max(1400px) {
    width: 100%;
    height: 100%;
    margin-bottom: 15px;
    margin-right: 0;
  }
  @include max(991px) {
    width: 155px;
    margin-bottom: 0;
    margin-right: 10px;
  }
  @include max(400px) {
    width: 100%;
    margin-right: 0;
    margin-bottom: 15px;
  }
  img{
    border-radius: 5px;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.widget .tagcloud a, 
.widget .wp-block-tag-cloud a,
.wp-block-tag-cloud a {
  padding: 9px 24px;
  margin-right: 6px;
  margin-bottom: 10px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px !important;
  line-height: 23px;
  display: inline-block;
  border-radius: 50px;
  letter-spacing: 0.5px;
  color: var(--heading-color);
  background: rgba(0, 0, 0, 0.05);
}
.widget .tagcloud a:hover, 
.widget .wp-block-tag-cloud a:hover,
.wp-block-tag-cloud a:hover{
  background-color: var(--accent-bg);
}

.wp-block-search__inside-wrapper{
  position: relative;
  input{
    font-weight: 400!important;
    border-radius: 50px!important;
    color: var(--heading-color)!important;
    border: 1px solid var(--variable-color1)!important;
    &::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
      color:    rgba(54, 56, 57, 1)!important;
      opacity: 1;
   }
   &:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */
     color:    rgba(54, 56, 57, 1)!important;
     opacity:  1;
   }
   &::-moz-placeholder{ /* Mozilla Firefox 19+ */
     color:    rgba(54, 56, 57, 1)!important;
     opacity:  1;
   }
   &:-ms-input-placeholder{ /* Internet Explorer 10-11 */
     color:    rgba(54, 56, 57, 1)!important;
     opacity: 1;
   }
   &::-ms-input-placeholder{ /* Microsoft Edge */
     color:    rgba(54, 56, 57, 1)!important;
     opacity: 1;
   }
   
   &::placeholder{ /* Most modern browsers support this now. */
     color:    rgba(54, 56, 57, 1)!important;
     opacity: 1;
   }
   
  }
}
button#wp-block-search__button{
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: var(--dark-bg);
}

// blog single

.post-thumbnail-details{
    margin-bottom: 30px;
    img{
        border-radius: 10px;
    }
}
.entry-content {
  h3{
    margin-bottom: 15px;
  }
  
  span{
    display: block;
    font-weight: 600;
    margin-bottom: 5px;
  }
  p{
    font-size: 18px;
    line-height: 30px;
  }
  
}
.post-tag {
  &-wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 25px 0;
    border-top: 1px solid #E6E6E6;
    border-bottom: 1px solid #E6E6E6;
    @include max(767px) {
      display: block;
    }
  }
  display: flex;
  align-items: center;
  @include max(767px) {
    margin-bottom: 20px;
  }
  
  h3{
    margin: 0;
    margin-right: 24px;
  }
  .wp-block-tag-cloud a{
    margin-bottom: 0;
  }
}

.post-like-comment{
  ul{
    li{
      margin-right: 24px;
      display: inline-block;
      &:last-child{
        margin-right: 0;
      }
      a{
        font-weight: 600;
        font-size: 16px;
        color: var(--body-color);
        img{
          margin-right: 6px;
        }
      }
    }
  }
}


ul.azzle-blog-details-list{
    margin-bottom: 30px;
    li{
        padding-left: 24px;
        font-size: 18px;
        line-height: 30px;
        position: relative;
        &::before{
            content: "";
            left: 5px;
            top: 10px;
            width: 7px;
            height: 7px;
            border-radius: 100px;
            position: absolute;
            background-color: var(--heading-color);
        }
    }
  }

// comment

.post-comment {
  padding-top: 40px;
  li{
    margin-bottom: 24px;
  }
  h3{
    margin-bottom: 30px;
  }
  &-wrap{
    display: flex;
  }
  
  &-thumb{
    width: 80px;
    height: 80px;
    flex-shrink: 0;
    overflow: hidden;
    border-radius:100%;
    margin-right: 20px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &-data{
    position: relative;
    padding-bottom: 24px;
    border-bottom: 1px solid #E6E6E6;
    strong{
      display: block;
      font-size: 16px;
      line-height: 1;
      margin-bottom: 5px;
    }
    span{
      font-weight: 400;
      font-size: 16px;
      display: block;
      margin-bottom: 10px;
    }
  }
  .children{
    padding-left: 85px;
    @include max(767px) {
      padding-left: 20px;
    }
    .post-comment-thumb{
      width: 50px;
      height: 50px;
    }
  }
}

a.comment-reply {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 16px;
  font-weight: 700;
  color: var(--body-color);
}

.comment-box{
  margin-top: 50px;
  h3{
    margin-bottom: 30px;
  }
  &-form{
    padding: 40px;
    border-radius: 10px;
    background-color: var(--light-bg5);
    border: 1px solid var(--heading-color);
    @include max(991px) {
      padding: 30px;
    }
  }
 
}

