.azzle-navigation {
  margin-top: 26px;
  text-align: center;
  .pagination{
    justify-content: center;
  }
}


.azzle-navigation a, 
.azzle-navigation span, 
.page-links a, 
.page-links span {
  height: 50px;
  width: 50px;
  display: inline-flex;
  text-align: center;
  line-height: 50px;
  font-size: 18px;
  font-weight: 700;
  align-items: center;
  justify-content: center;
  margin: 0 5px;
  color: var(--body-color);
  border-radius: 50px;
}

.azzle-navigation a:hover, 
.page-links a:hover {
  background-color: var(--heading-color);
  color: var(--light-color);
}

.azzle-navigation span.current, 
.page-links .current {
  cursor: not-allowed;
  background-color: var(--heading-color);
  color: var(--light-color);
}

.next{
  background-color: transparent!important;
}