.pre-item {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 99999;
  height: 100%;
  width: 100%;
  overflow: hidden;
  background: var(--heading-bg);
}

.textWrapper {
  height: -moz-fit-content;
  height: fit-content;
  min-width: 3rem;
  width: -moz-fit-content;
  width: fit-content;
  position: relative;
  z-index: 0;
  color: white;
  p{
    font-size: 80px;
    font-weight: 500!important;
    letter-spacing: 0.05em;
    margin: 0;
    line-height: 1;
    @include max(1199px) {
      font-size: 65px;
    }
     @include max(991px) {
      font-size: 55px;
    }
 
  }
}

.invertbox {
  position: absolute;
  height: 100%;
  aspect-ratio: 1/1;
  left: 0;
  top: 0;
  border-radius: 20%;
  background-color: rgba(255, 255, 255, 0.1);
  -webkit-backdrop-filter: invert(100%);
  backdrop-filter: invert(100%);
  animation: move 2s ease-in-out infinite;
}

@keyframes move {
  50% {
    left: calc(100% - 3rem);
  }
}
img {
  image-rendering: -webkit-optimize-contrast;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
}

@keyframes slideLeft {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}
@keyframes slideRight {
  0% {
    opacity: 1;
    transform: translateX(0%);
  }
  100% {
    opacity: 0;
    transform: translateX(100%);
  }
}