.azzle-portfolio{
  &-column{
    gap: 40px;
    display: flex;
    flex-direction: column;
    @include max(991px) {
      margin-bottom: 40px;
    }
  }
  &-box{
    &:hover .azzle-portfolio-thumb{
      img{
        transform: scale(1.1);
      }
    }
    &:hover .azzle-portfolio-icon svg path{
      stroke: var(--accent-color);
    }
  }
  &-thumb{
    border-radius: 10px;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all 0.4s;
    }
  }
  &-content{
    gap: 150px;
    margin-top: 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    h3{
      font-size: 30px;
      @include max(767px) {
        font-size: 24px;
      }
    }
    @include max(1399px) {
      gap: 100px;
    }
    @include max(1199px) {
      gap: 50px;
    }
    @include max(991px) {
      gap: 200px;
    }
    @include max(767px) {
      gap: 50px;
      
    }
  }
  &-icon{
    svg{
      path{
        transition: all 0.4s;
      }
    }
  }
}

// portfolio details
.azzle-portfolio-details{
  &-top{
    padding-bottom: 80px;
    margin-bottom: 80px;
    border-bottom: 1px solid var(--variable-color1);
    @include max(767px) {
      padding-bottom: 60px;
      margin-bottom: 60px;
    }
  }
  &-thumb{
    margin-bottom: 50px;
    img{
      border-radius: 10px;
    }
  }
  &-info{
    display: flex;
    justify-content: space-between;
    @include max(991px) {
      flex-wrap: wrap;
      column-gap: 50px;
      row-gap: 30px;
      justify-content: flex-start;
    }
  }
  &-item{
    p{
      margin-bottom: 15px;
      color: var(--border-color);
    }
    a{
      font-size: 28px;
      font-weight: 700;
      color: var(--heading-color);
      @include max(767px) {
        font-size: 24px;
      }
      img{
        margin-left: 10px;
      }
    }
  }
}