
.azzle-default-btn,
#azzle-submit-btn {
  font-size: $button_font_size;
  line-height: $buton_line_height;
  padding: $button_padding;
  font-weight: $button_font_weight;
  font-family: $button_font_family;
  border-radius: $buton_border_radius;
  color: var(--light-color);
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  text-align: center;
  overflow: hidden;
  position: relative;
  transition: all 0.4s;
  transform-style: preserve-3d;
  white-space: nowrap;
  text-overflow: ellipsis;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  --arrow-hover-move-x: -110%;
  background-color: var(--heading-bg);
  @include max (767px){
    padding: 15px 24px;
  }
  &.btn2{
    background-color: var(--variable-color4);
    align-items: center;
    gap: 10px;
  }
  &.btn3{
    background-color: var(--variable-color4);
    align-items: center;
    gap: 10px;
    padding: 18px 35px;
  }
  &.btn4{
    background-color: var(--variable-color4);
    align-items: center;
    gap: 10px;
    padding: 13px 28px;
  }
  &.btn5{
    border-radius: 3px;
    color: var(--heading-color);
    background-color: var(--variable-color6);
    padding: 13px 28px;
      &:hover{
      color: var(--heading-color);
    }
  }
  
  &:hover{
    color: var(--light-color);
  }
  &.azzle-header-btn{
    padding: 14px 24px;
  }
  &.outline-btn{
    padding: 16px 34px;
    color: var(--heading-color);
    border: 2px solid var(--heading-color);
    background-color: var(--light-color2);
    &:hover{
      border: 2px solid transparent;
      background-color: var(--accent-bg);
    }
    &.btn2{
      color: var(--heading-color);
      background-color: var(--accent-color);
      &:hover{
        background-color: var(--light-color2);
      }
    }
  }
  &.outline-btn3{
    width: 100%;
    padding: 16px 34px;
    color: var(--heading-color);
    border: 2px solid var(--heading-color);
    background-color: var(--light-color5);
    justify-content: center;
    &:hover{
      color: var(--light-color);
      background-color:var(--variable-color4);
       border: 2px solid var(--variable-color4);
    }
  }
  &.d-block {
    width: 100%;
    display: flex!important;
    justify-content: center;
  }
  &:focus {
    box-shadow: none;
  }
}




.azzle-default-btn::before,
#azzle-submit-btn::before {
  position: absolute;
  content: '';
  border-radius: inherit;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
  z-index: -1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-size: 102% 102%;
  opacity: 0;
}
.azzle-default-btn .button-wraper, 
#azzle-submit-btn .button-wraper {
  transition: opacity 0.2s, transform 0.2s;
  transition-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
.azzle-default-btn:hover .button-wraper, 
#azzle-submit-btn:hover .button-wraper {
  transform: translateY(-150%);
  opacity: 0;
}
.azzle-default-btn:hover::before, 
#azzle-submit-btn:hover::before  {
  opacity: 1;
  transition: all 0.2s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
.azzle-default-btn:hover::after, 
#azzle-submit-btn:hover::after {
  transform: translate(0%, -50%);
  opacity: 1;
}


.azzle-default-btn:after, 
#azzle-submit-btn:after {
  content: attr(data-text);
  display: inline-block;
  position: absolute;
  top: 50%;
  opacity: 0;
  line-height: 12.5px;
  transform: translate(0, 100%);
  transition: opacity 0.2s, transform 0.2s;
  transition-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
  white-space: nowrap;
}



// add animation and hover

.azzle-default-btn svg {
  transition: all 0.3s ease-out 0s;
}
.azzle-default-btn:hover svg path:nth-of-type(1), .azzle-default-btn:focus-visible svg path:nth-of-type(1) {
  transform: translateX(calc(-1* var(--arrow-hover-move-x)));
  opacity: 0;
  transition-delay: 0s, 0s;
}
.azzle-default-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.azzle-default-btn svg path {
  transition: transform 0.38s cubic-bezier(0.37, 0.08, 0.02, 0.93), opacity 0.18s ease-out;
}
.azzle-default-btn:hover svg path:nth-of-type(2), .azzle-default-btn:focus-visible svg path:nth-of-type(2) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.azzle-default-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}
.azzle-default-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.azzle-default-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}



.azzle-blog-btn svg {
  transition: all 0.3s ease-out 0s;
}
.azzle-blog-btn:hover svg path:nth-of-type(1), .azzle-blog-btn:focus-visible svg path:nth-of-type(1) {
  transform: translateX(calc(-1* var(--arrow-hover-move-x)));
  opacity: 0;
  transition-delay: 0s, 0s;
}
.azzle-blog-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.azzle-blog-btn svg path {
  transition: transform 0.38s cubic-bezier(0.37, 0.08, 0.02, 0.93), opacity 0.18s ease-out;
}
.azzle-blog-btn:hover svg path:nth-of-type(2), .azzle-blog-btn:focus-visible svg path:nth-of-type(2) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.azzle-blog-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}
.azzle-blog-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.azzle-blog-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}


// icon hover

.azzle-icon-hover{
  overflow: hidden;
  position: relative;
  display: inline-flex;
  align-items: center;
  &:hover{
    .hover-icon-show{
      transform: translateX(25px);
    }
  }
  &:hover{
    .hover-icon-hide{
      transform: translateX(0px);
      &.small-icon{
        transform: translateX(-20px);
      }
    }
  }
}

.hover-icon-hide {
    transition: all 0.4s;
    position: absolute;
    transform: translateX(-52px);
}
.hover-icon-show{
  transition: all 0.4s;
  
}