.azzle-core-value{
  &-section{
    padding: 130px 0;
    border-radius: 50px;
    margin: 0 50px;
    background-color: var(--heading-color);
    @include max(1199px) {
      border-radius: 0;
      margin: 0;
    }
    @include max(991px) {
      padding: 100px 0;
    }
    @include max(767px) {
      padding: 80px 0;
    }
  }
  &-column{
    gap: 24px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    border-top: 5px solid var(--variable-color2);
    @include max(1199px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @include max(767px) {
      gap: 0;
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &-header{
    display: flex;
    gap: 12px;
    margin-bottom: 24px;
    img{
      flex-shrink: 0;
    }
    h3{
      font-size: 30px;
      color: var(--light-color);
    }
  }
  &-item{
    position: relative;
    padding-top: 40px;
    &::before{
      content: "";
      left: 50%;
      top: -5px;
      height: 5px;
      width: 0;
      position: absolute;
      transition: all 0.4s;
      transform: translateX(-50%);
      background-color: var(--accent-color);
      @include max(1199px) {
        content: none;
      }
    }
    &:hover::before{
      width: 100%;
    }
    p{
      color: var(--light-color);
    }
  }
}