.azzle-cta{
    &-slider{
        &-title{
            font-size: 120px;
            line-height: 1.1em;
            font-weight: 700;
            @include max(1399px) { 
                font-size: 100px;
            } 
            @include max(991px) { 
                font-size: 80px;
            }
            @include max(767px) { 
                font-size: 60px;
            }
            @include max(575px) { 
                font-size: 42px;
            }
        }
        &-init{
            display: flex;
            gap: 24px;
            position: relative;
        }
     &-item{
        gap: 24px;
        display: flex;
        flex-shrink: 0;
        img{
              animation: spin 7s linear infinite;

        }
     }
    }
    &-title3{
        h1{
            color: var(--light-color5);
            font-family: $heading-font3;
            font-size: 120px;
            line-height: 1em;
            letter-spacing: -1px;
             @include max(1399px) { 
                font-size: 100px;
            } 
            @include max(991px) { 
                font-size: 80px;
            }
            @include max(767px) { 
                font-size: 60px;
            }
            @include max(575px) { 
                font-size: 42px;
            }
        }
    }
    &-content3{
        padding-left: 30px;
        @include max (991px){
            padding-left: 0;
            padding-top: 40px;
            padding-right: 100px;
            @include max (767px){
                padding-right: 80px;
            }
        }
        @include max (767px){
            padding-top: 30px;
            padding-right: 0;
        }
        p{
            font-weight: 600;
            font-size: 24px;
            line-height: 1.33em;
            color: var(--light-color5);
        }
    }
    &-contact-info{
        @include max (1199px){
            margin-bottom: 24px;
        }
        ul{
            li{
                a{
                    font-family: $heading-font3;
                    font-weight: 700;
                    font-size: 30px;
                    line-height: 1.5em;
                    text-align: center;
                    padding: 20px 43px;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 10px;
                    color: var(--light-color5);
                    transition: all 0.4s;
                    background-color: var(--light-color6);
                    &:hover{
                        background-color: var(--variable-color5);
                    }
                    @include max (1399px){
                        font-size: 22px;
                    }
                }
            }
        }
        &.info2{
            ul{
                li{
                    font-family: $heading-font3;
                    font-weight: 700;
                    font-size: 30px;
                    line-height: 1.5em;
                    text-align: center;
                    padding: 20px 43px;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 10px;
                    color: var(--light-color5);
                    transition: all 0.4s;
                    background-color: var(--light-color6);
                    &:hover{
                        background-color: var(--variable-color5);
                    }
                    @include max (1399px){
                        font-size: 22px;
                    }
                }
            }
        }
    }
}





@keyframes spin{
  from { transform: rotate(0deg); }
  to   { transform: rotate(360deg); }
}