.azzle-contact-info{
  &-wrap{
    gap: 50px;
    display: flex;
    flex-direction: column;
  }
  &-item{
    h5{
      font-size: 24px;
      margin-bottom: 15px;
    }
    a{
      font-size: 30px;
      color: var(--heading-color);
      @include max(991px) {
        font-size: 24px;
      }
    }
  }
}

.azzle-contact{
  &-column{
    gap: 24px;
    display: grid;
    margin-bottom: 24px;
    grid-template-columns: repeat(2, 1fr);
    @include max(991px) {
       grid-template-columns: repeat(1, 1fr);
    }
  }
  &-box{
    padding: 50px 30px;
    border-radius: 10px;
    background-color: var(--light-color);
    box-shadow: 0px 4px 60px 0px rgba(0, 0, 0, 0.1);
    @include max(991px) {
      margin-top: 40px;
    }
  }
  &-field{
    label{
      font-size: 18px;
      font-weight: 700;
      margin-bottom: 10px;
    }
  }
}


#azzle-main-submit-btn {
  width: 203px;
  height: 60px;
  margin-top: 24px;
  display: inline-block;
  border-radius: 50px;
  font-size: 16px;
  font-weight: 700;
  transition: all 0.4s;
  color: var(--light-color);
  background-color: var(--heading-color);
  &:hover{
    background-color: var(--accent-color);
  }
}



.azzle-contact-map{
  &-section{
    padding: 130px 0 180px;
    background-color: var(--heading-color);
    @include max(991px) {
      padding: 80px 0 130px;
    }
    @include max(767px) {
      padding: 60px 0 110px;
    }
  }
}

.azzle-location{
  &-map{
    position: relative;
  }
  &-wrap{
    cursor: pointer;
    position: absolute;
    &:hover .azzle-location-icon{
      svg{
        transform: scale(1.2);
        path{
          fill: var(--accent-color);
        }
      }
    }
    &:hover .azzle-location-box{
      top: -70px;
      opacity: 1;
    }
  }
  &-icon {
    svg {
      transition: all 0.4s;
      path{
        transition: all 0.4s;
      }
      
    }
  }
  &-box{
    padding: 15px;
    width: 169px;
    border-radius: 8px;
    background-color: var(--light-color);
    position: absolute;
    left: 40px;
    top: -50px;
    opacity: 0;
    transition: all 0.4s;
    @include max(1199px) {
      left: auto;
      right: 126%;
    }
    @include max(767px) {
      display: none;
    }
    p{
      font-weight: 600;
      font-size: 14px;
      margin: 0;
      line-height: 20px;
    }
  }
  &-header{
    margin-bottom: 15px;
    display: flex;
    gap: 10px;
    align-items: center;
    p{
      margin: 0;
      font-size: 14px;
      font-weight: 700;
      line-height: 20px;
    }
  }
}

.location-one {
    top: 10%;
    left: 14%;
}

.location-two {
    bottom: 26%;
    left: 20%;
}

.location-three {
    top: 10%;
    right: 36%;
}


.location-four {
    bottom: 19%;
    right: 10%;
}

// account page css
.mb24{
  margin-bottom: 24px;
}
.azzle-account{
  &-section{
    position: relative;
    padding: 130px 0;
    z-index: 1;
    @include max(991px) {
      padding: 100px 0;
    }
    @include max(991px) {
      padding: 80px 0;
    }
  }
  &-box{
    border-radius: 10px;
    padding: 40px;
    max-width: 856px;
    margin: 0 auto;
    background-color: var(--light-color);
    box-shadow: 0px 4px 60px 0px rgba(0, 0, 0, 0.1);
    @include max(991px) {
      padding: 30px;
    }
  }
}
.azzle-account-header{
  text-align: center;
  margin-bottom: 80px;
  @include max(991px) {
    margin-bottom: 60px;
  }
}
.azzle-check-field {
  display: block;
  margin-bottom: 25px;
  @include max(991px) {
    margin-bottom: 10px;
  }
}

.azzle-check-field input {
  padding: 0;
  height: initial;
  width: initial;
  margin-bottom: 0;
  display: none;
  cursor: pointer;
}

.azzle-check-field label {
  position: relative;
  cursor: pointer;
  font-size: 16px;
  line-height: 24px;
}

.azzle-check-field label:before {
  content:'';
  -webkit-appearance: none;
  background-color: transparent;
  border: 2px solid var(--border-color);
  padding: 6px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  cursor: pointer;
  margin-right: 10px;
  border-radius: 3px;
  margin-top: -1px;
}

.azzle-check-field input:checked+label:after {
    content: "";
    display: block;
    position: absolute;
    top: 8px;
    left: 6px;
    width: 5px;
    height: 8px;
    border: solid var(--heading-color);
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
}

.azzle-account-or{
  padding: 40px 0;
  position: relative;
  text-align: center;
  z-index: 0;
  @include max(991px) {
    padding: 24px 0;
  }
  &::before{
    content: "";
    left: 0;
    top: 55px;
    width: 100%;
    height: 1px;
    z-index: -1;
    position: absolute;
    background-color: var(--variable-color1);
    @include max(991px) {
      top: 40px;
    }
  }
  p{
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    padding: 0 15px;
    display: inline-block;
    text-transform: uppercase;
    background-color: var(--light-color);
  }
}



.azzle-account-social {
    &-wrap{
      display: flex;
      gap: 24px;
      margin-bottom: 40px;
      flex-direction: column;
      @include max(991px) {
        margin-bottom: 24px;
      }
    }
    display: flex;
    gap: 10px;
    font-size: 16px;
    font-weight: 700;
    align-items: center;
    width: 100%;
    padding: 14px;
    justify-content: center;
    border-radius: 50px;
    color: var(--heading-color);
    border: 1px solid var(--variable-color1);
}

.azzle-accout-footer {
  text-align: center;
  p{
    font-size: 16px;
    a{
      font-weight: 600;
      color: var(--heading-color);
    }
  }
}


.azzle-forget-password {
    display: flex;
    justify-content: space-between;
    a{
      font-size: 16px;
      color: var(--heading-color);
    }
}

.mt24{
  margin-top: 24px;
}