.azzle-faq{
    &-wrap1{
      @include max (991px){
        padding-top: 40px;
      }

      &.wrap2{
        @include max (991px){
          padding-top: 0;
        }
      }
    }
    &-item{
      padding-bottom: 24px;
      border-bottom: 1px solid var(--light-color3);
      margin-bottom: 24px;
      transition: all 0.4s;
      &:last-child{
          border-bottom: none;
          padding-bottom: 0;
          margin-bottom: 0;
      }
      &.open {
        .azzle-faq-header{
          padding-bottom: 10px;
        }
        .azzle-faq-body{
          display: block;
        }
        .azzle-active-icon img{
          transform: rotate(45deg);
        }
      }
      &.item2{
        padding-bottom: 0;
        border: none;
        margin-bottom: 24px;
        padding: 24px;
        border-radius: 20px;
        border: 1px solid var(--variable-color1);
        &.open{
          box-shadow: 0px 4px 60px 0px rgba(0, 0, 0, 0.1);
          border: 1px solid transparent;
          background-color: var(--light-color);
          .azzle-active-icon2{
            width: 50px;
            height: 50px;
            border-radius: 100px;
            background-color: var(--variable-color4);
          }
        }
        &:last-child{
          margin-bottom: 0;
        }
      }
      &.item3{
        background-color: var(--light-color6)!important;
        margin-bottom: 24px;
        border: none;
        padding: 24px;
        border-radius: 10px;
        &.open{
          .azzle-active-icon{
            transform: rotate(180deg);
          }
          .azzle-faq-body.body2{
            p{
              color: var(--light-color);
            }
          }
        }
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
    &-header{
      cursor: pointer;
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
      transition: all 0.4s;
      h3{
        @include max(767px) {
          font-size: 24px;
          padding-right: 25px;
        }
      }
      &.header2{
        h3{
          font-weight: 500;
          font-family: $heading-font2;
        }
      }
      &.header3{
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
    }
    &-body{
      display: none;
    }
    &-icon{
      display: flex;
      align-items: center;
      gap: 30px;
      h3{
        font-weight: 700;
        font-size: 30px;
        line-height: 1.5em;
        color: var(--light-color);
        letter-spacing: -1px;
      }
    }
}

 .azzle-faq-body{
  padding-right: 20px;
    p{
      font-size: 18px;
      line-height: 1.67em;
      color: var(--variable-color3);
    }
    &.body2{
      padding-right: 100px;
      @include max(1199px) {
        padding-right: 24px;
      }
    }
}

// 

.azzle-active-icon2{
  transition: all .4s;
  display: flex;
  width: 50px;
  flex-shrink: 0;
  height: 50px;
  text-align: center;
  align-items: center;
  border-radius: 50px;
  justify-content: center;
  background-color: var(--variable-color1);
  @include max (767px){
    width: 40px;
    height: 40px;
  }
  img{
    transition: all 0.4s;
  }
}

.azzle-faq-item.item2.open {
  img.mynusicon{
    opacity: 1!important;
  }
  img.plasicon{
    opacity: 0;
    display: block;
  }
}
img.plasicon {
  opacity: 1;
  position: relative;
}
img.mynusicon {
  opacity: 0;
  position: absolute;
}


.azzle-faq-item.item3.open {
  img.mynusicon{
    opacity: 1!important;
  }
  img.plasicon{
    opacity: 0;
    display: block;
  }
}


.azzle-active-icon img{
  transition: all 0.4s;
}

// faq page

.azzle-faq-wrap1.faq-page{
  padding-top: 0;
  display: flex;
  flex-direction: column;
  .azzle-faq-item{
    padding: 24px;
    border-radius: 10px;
    border: 1px solid var(--variable-color1);
    &.open{
      border: 1px solid var(--light-color);
        box-shadow: 0px 4px 60px 0px rgba(0, 0, 0, 0.1);
    }
  }
  .azzle-faq-header{
    h3{
      font-weight: 500;
    }
  }
  .azzle-faq-body {
    padding-right: 130px;
    @include max(1199px) {
      padding-right: 50px;
    }
    @include max(767px) {
      padding-right: 0px;
    }
  }
}