input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  font-weight: 600;
  border-radius: 10px;
  background-color: transparent;
  padding: 15px 24px;
  border: 1px solid var(--light-color8);
  transition: all 0.4s;
  font-size: 16px;
  line-height: 24px;
  &::placeholder{
    color: var(--border-color);
  }
  &:focus{
    border: 1px solid var(--accent-color);
  }
}
textarea {
  height: 180px;
  resize: none;
  @include max(767px) {
    height: 130px;
  }
}


.azzle-subscribe{
  &-field{
    position: relative;
    input{
      font-weight: 400;
      height: 50px;
      color: var(--heading-color);
      padding: 13px 70px 13px 24px;
      border-radius: 50px!important;
      border: 1px solid var(--heading-color)!important;

      &::placeholder{
        color: var(--heading-color);
      }
    }
  }
  &-field2{
    position: relative;
    input{
      width: 500px;
      height: 60px;
      color: var(--light-color);
      padding: 13px 190px 13px 20px;
      border-radius: 50px!important;
      border: 1px solid var(--light-color)!important;
      @include max (767px){
        width: 100%;
      }
      &::placeholder{
        color: var(--light-color);
      }
    }
  }

}

.sub-btn {
  width: 60px;
  height: 40px;
  background-color: #000;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 5px;
  right: 5px;
  transition: all 0.4s;
  &:hover{
    background-color: var(--accent-color);
  }
}
.sub-btn2{
  position: absolute!important;
  top: 5px;
  right: 145px;
  @include max (991px){
    right: 105px;
  }
  @include max (767px){
    right: 5px;
  }
}
.sub-btn3{
  position: absolute!important;
  top: 5px;
  right: 31.5%;
  height: 50px;
  color: var(--heading-color)!important;
  background-color: var(--variable-color6)!important;
  border-radius: 3px !important;
  @include max (991px){
    right: 105px;
  }
  @include max (767px){
    right: 5px;
  }
}

.azzle-blog-subscriber{
  input{
    font-weight: 400;
    height: 55px;
    border-radius: 50px;
    border: 1px solid var(--heading-color);
    &::placeholder{
      color: var(--heading-color);
    }
  }
}

#azzle-blog-subscribe{
  width: 100%;
  height: 55px;
  display: flex;
  margin-top: 15px;
  font-size: 16px;
  font-weight: 700;
  align-items: center;
  border-radius: 50px;
  justify-content: center;
  color: var(--light-color);
  transition: all 0.4s;
  background-color: var(--heading-color);
  &:hover{
    background-color: var(--accent-color);
  }
}