.azzle{
  &-breadcrumb{
    text-align: center;
    padding: 195px 0 130px;
    position: relative;
    transition: all 0.4s;
    border-radius: 0 0 50px 50px;
    background-color: var(--light-color2);
    @include max(1199px) {
      padding: 180px 0 100px;
      border-radius: 0 0 30px 30px;
    }
    @include max(767px) {
      padding: 150px 0 80px;
      border-radius: 0 0 15px 15px;
    }
     @include max(767px) {
      padding: 120px 0 60px;
    }

  &-content {
    text-align: center;
    margin: 0 auto;
  }
  &-title{
    margin-bottom: 50px;
    @include max(1199px) {
      margin-bottom: 35px;
    }
    @include max(767px) {
      margin-bottom: 24px;
    }
  }

  &-menu{
    ul{
      li{
        font-weight: 600;
        line-height: 32px;
        display: inline-block;
        padding: 0 6px;
        color: var(--light-color);
        @include max(479px) {
          font-size: 18px;
        }
        a{
            color: var(--heading-color);
        }
        &:last-child{
          color: var(--accent-color);
        }
      }
    }
    &-wrap{
      padding-top: 50px;
      @include max (1199px){
        padding-top: 30px;
      }
    }
  }
}
}




