// section title

.azzle-section-title{
    padding-bottom: 80px;
    @include max (991px){
        padding-bottom: 60px;
    }
    @include max (767px){
        padding-bottom: 40px;
    }
    &.max-w-650{
        max-width: 650px;
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-700{
            max-width: 700px;
        }
        &.max-width-750{
            max-width: 750px;
        }
        &.max-width-800{
            max-width: 800px;
        }
        &.max-width-850{
            max-width: 850px;
        }
        &.max-width-950{
            max-width: 950px;
        }
    }
    &.max-width-780{
        max-width: 780px;
    }
    &.max-w-800{
        max-width: 880px;
    }
    &.max-w-1000{
        max-width: 1000px;
    }
    &.font-title{
        h2{
            font-family: $heading-font2;
            font-weight: 500;
        }
    }
    &.title2{
        h2{
            color: var(--light-color);
        }
    }
    &.title3{
        h2{
            font-family: $heading-font3;
            font-weight: 700;
            font-size: 80px;
            line-height: 1em;
            letter-spacing: -1px;
            @include max (1199px){
                font-size: 70px;
            }
            @include max (991px){
                font-size: 48px;
            }
            @include max (767px){
                font-size: 40px;
            }
        }
        &.title4{
            h2{
                color: var(--light-color5);
            }
        }
    }
    &.title4{
        h2{
            color: var(--light-color);
        }
    }
    &.max-w-900{
        max-width: 900px;
    }
}
.section-title-button {
    width: 100%;
    text-align: right;
    @include max(991px) {
        text-align: center;
        margin-top: 30px;
    }
}
.two-column{
    @include max(991px) {
        text-align: center;
    }
}
.accent-bg2{
    border-radius: 20px;
    background-color: var(--variable-color5);
}

// default content
.azzle-default-content{
    h2{
        padding-bottom: 20px;
    }
    h3{
        padding-bottom: 30px;
    }
    h4{
        font-size: 24px;
        margin-bottom: 30px;
    }
    // ul{
