.azzle-single-thumb{
  img{
    border-radius: 20px;
    @include max(575px) {
      height: 220px;
      object-fit: cover;
      border-radius: 10px;
    }
  }
}

.azzle-content-section{
  padding: 130px 0 180px;
  background-color: var(--heading-bg);
  @include max(991px) {
    padding: 100px 0 150px;
  }
  @include max(767px) {
    padding: 80px 0 130px;
  }
}

.pl-30{
  padding-left: 30px;
  @include max (1199px){
    padding-left: 0;
  }
}

.azzle-contact-info {
  ul{
    li{
      font-size: 30px;
      font-weight: 700;
      font-family: $heading-font;
      color: var(--accent-color);
      margin-bottom: 24px;
      @include max(991px) {
        font-size: 24px;
        margin-bottom: 15px;
      }
      &:last-child{
        margin-bottom: 0;
      }
      a{
        margin-left: 7px;
        color: var(--light-color);
      }
    }
  }
}